<template lang="pug">
.contentContainer
    h1.TitleU {{name}}
    h2.subTitle Keynote Speakers
    .speakerContainer(v-for="speaker in $route.query.year?speakers[$route.query.year]:speakers[2022]" data-aos="fade-up" data-aos-duration="1000")
