                    name: "Pedro Lopes",
                    agency: "University of Chicago, USA",
                    keynote: "Keynote 1",
                    content: "Human Computer Integration: Powering a New Generation of Interactive Devices",
                    summary: '<p><span style="font-weight: 400;">When we look back to the early days of computing, user and device were distant, often located in separate rooms. Then, in the &rsquo;70s, personal computers &ldquo;moved in&rdquo; with users. In the &rsquo;90s, mobile devices moved computing into users&rsquo; pockets. More recently, wearable devices brought computing into constant physical contact with the user&rsquo;s skin. These transitions proved useful: moving closer to users allowed interactive devices to sense more of their user and act more personal. The main question that drives my research is: </span><strong>what is the next interface paradigm that supersedes wearable devices?</strong></p><p><span style="font-weight: 400;">The primary way researchers have been investigating this is by asking where future interactive devices will be located with respect to the user&rsquo;s body. Many posit that the next generation of interfaces will be implanted inside the user&rsquo;s body. However, I argue that their location with respect to the user&rsquo;s body is not the primary factor; in fact, implanted devices are already happening in that we have pacemakers, insulin pumps, etc. Instead, I argue that the key factor is how will devices </span><strong>integrate</strong><span style="font-weight: 400;"> with the user&rsquo;s biological senses and actuators.&nbsp;</span></p><p><span style="font-weight: 400;">This body-device integration allows us to engineer interactive devices that intentionally borrow parts of the body for input and output, rather than adding more technology to the body. For example, one such type of body-integrated devices, which I have advanced recently, are interactive systems based on electrical muscle stimulation. These devices are able to move their user&rsquo;s muscles using computer-controlled electrical impulses, achieving the functionality of robotic exoskeletons without the bulky motors.&nbsp;</span></p><p><span style="font-weight: 400;">They key insight of my research is that engineering devices that intentionally borrow parts of the user&rsquo;s biology puts forward a </span><strong>new</strong> <strong>generation of miniaturized devices</strong><span style="font-weight: 400;">; allowing us to circumvent traditional physical constrains. For instance, in the case of our devices based on electrical muscle stimulation, they demonstrate how our body-device integration circumvents the constrains imposed by ratio of electrical power and size of a motor (i.e., the stronger/larger a motor is, more current needed to actuate it). Similarly, we demonstrate how our body-device integration approach </span><strong>even allowed us to miniaturize thermal feedback</strong><span style="font-weight: 400;"> (hot/cold sensations) without the need for power-hungry devices like Peltiers, air conditioners or heaters.</span></p>',
