ht: 400;">They key insight of my research is that engineering devices that intentionally borrow parts of the user&rsquo;s biology puts forward a </span><strong>new</strong> <strong>generation of miniaturized devices</strong><span style="font-weight: 400;">; allowing us to circumvent traditional physical constrains. For instance, in the case of our devices based on electrical muscle stimulation, they demonstrate how our body-device integration circumvents the constrains imposed by ratio of electrical power and size of a motor (i.e., the stronger/larger a motor is, more current needed to actuate it). Similarly, we demonstrate how our body-device integration approach </span><strong>even allowed us to miniaturize thermal feedback</strong><span style="font-weight: 400;"> (hot/cold sensations) without the need for power-hungry devices like Peltiers, air conditioners or heaters.</span></p>',
                    biography: '<p><a href="https://lab.plopes.org/"><span style="font-weight: 400;color: rgb(17, 85, 204);text-decoration: underline;">Pedro Lopes</span></a><span style="font-weight: 400;"> is an Assistant Professor in Computer Science at the University of Chicago, where he leads the </span><strong>Human Computer Integration lab</strong><span style="font-weight: 400;">. Pedro focuses on </span><strong>integrating computer interfaces with the human body&mdash;exploring the interface paradigm that supersedes wearable computing</strong><span style="font-weight: 400;">. Some of these new integrated-devices include: a device based on muscle stimulation that allows </span><a href="https://www.youtube.com/watch?v=Gz4dphzBb6I"><span style="font-weight: 400;color: rgb(17, 85, 204);    text-decoration: underline;">users to manipulate tools they never seen before</span></a><span style="font-weight: 400;"> or that </span><a href="https://www.youtube.com/watch?v=1BT8REEJibM"><span style="font-weight: 400;color: rgb(17, 85, 204);text-decoration: underline;">accelerate their reaction time</span></a><span style="font-weight: 400;">, or a device that </span><a href="https://www.youtube.com/watch?v=pH68GNkb_fA&amp;feature=youtu.be"><span style="font-weight: 400;color: rgb(17, 85, 204); text-decoration: underline;">leverages the sense of smell to create an illusion of temperature</span></a><span style="font-weight: 400;">. </span><span style="font-weight: 400;">Pedro&rsquo;s work is published at top-tier conferences (ACM CHI &amp; ACM UIST), where he has received four Best Paper awards, two Best Paper nominations and several Best Talk/Demo/Video awards. Pedro&rsquo;s work also captured the interest of media, such as New York Times, MIT Technology Review, NBC, Discovery Channel, NewScientist, Wired and has been shown at Ars Electronica and World Economic Forum (More: </span><a href="https://lab.plopes.org/"><span style="font-weight: 400;color: rgb(17, 85, 204);text-decoration: underline;">https://lab.plopes.org</span></a><span style="font-weight: 400;">)</span></p>'
                },
                {
                    img: "https://pbs.twimg.com/profile_images/502132318354419713/UXyxLGjS_400x400.jpeg",
                    name: "Mike Chen",
                    agency: "National Taiwan University, Taiwan",
                    keynote: "Keynote 2",
                    content: "Playing with Perception - Haptics Re-imagined",
                    summary: '<p><span style="font-weight: 400;">Motion simulator was invented over 100 years ago and modern haptic feedback devices were commercialized over 50 years ago. In this talk, we will take a fresh look at the fundamental limitations that have been in place ever since, and re-imaging how these experiences can be perceptually designed to be more realistic, immersive, and enable greater mobility.</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff;" href="https://dl.acm.org/doi/10.1145/3386569.3392482"> <span style="font-weight: 400;">HeadBlaster</span></a></span></span><span style="font-weight: 400;">, is the first wearable approach to recreating the persistent proprioception and vestibular stimulation during real acceleration, enabling it to provide significantly longer motion sensation than motion platforms.</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff; text-decoration: underline;" href="https://dl.acm.org/doi/abs/10.1145/3313831.3376292"> MiniatureHaptics</a></span></span><span style="font-weight: 400;"> explores whether haptic feedback can be remapped to just the hand, in order to enable new haptic experiences that are not practical to create at the full, human-body scale.&nbsp;&nbsp;</span></p><p><span style="font-weight: 400;">We will further explore research opportunities in applying perceptual design to haptic feedback, such as a multi-sensory approach that utilizes noise, which is normally undesir</span><span style="font-weight: 400;">able, to enhance haptic experiences, as well as perceptual approaches to address limitations of existing force feedback technologies. Last, we will discuss fun research and design opportunities by shifting focus from reproducing real-world experiences to out-of-this-world experiences. </span></p>',
                    biography: '<p><a href="https://mikechen.com/"><span style="font-weight: 400;"><span style="text-decoration: underline;"><span style="color: #3366ff; text-decoration: underline;">Prof. Mike Y. Chen</span></span></span></a><span style="font-weight: 400;"> creates future user experiences, and innovates at the intersection of HCI, AI, design, perception, and VR/AR. He founded the</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff;" href="https://ntuhci.org/"> <span style="font-weight: 400;">HCI Lab</span></a></span></span><span style="font-weight: 400;"> at National Taiwan University, which has published at premier ACM conferences including CHI, SIGGRAPH, and UIST, and received Best Talk/Paper and Innovative Game Design Awards. He has</span><span style="text-decoration: underline;"><span style="color: #3366ff; text-decoration: underline;"><a style="color: #3366ff; text-decoration: underline;" href="https://github.com/ntu-hci-lab"> <span style="font-weight: 400;">open-sourced projects</span></a></span></span><span style="font-weight: 400;"> ranging from hardware designs of haptic systems to touchscreen gesture data analytics for ResearchKit, which was mentioned at Apple WWDC 2019.</span></p><p><span style="font-weight: 400;">Mike received his Ph.D. in Computer Science from UC Berkeley, with a Management of Technology (MOT) certificate from Haas School of Business. Prior to NTU, Mike led Mobile R&amp;D at Intel Research and startup Ludic Labs.&nbsp;</span></p><p><span style="font-weight: 400;">For more info: see</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff;" href="https://mikechen.com/"> <span style="font-weight: 400;">https://mikechen.com/</span></a></span></span></p>'
                }
            ]
            }
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import "@/style/value.sass"
.speakerContainer 
