<template lang="pug">
.contentContainer
    h1.TitleU {{name}}
    .HTMLContainer(v-html="$route.query.year?body[$route.query.year]:body[2022]")
</template>

<script>

export default {
    components: {

    },
    data () {
        return {
            name: "ABOUT THE WORKSHOP",
            body: {
                2022: '<p dir="ltr" style="line-height: 1.7; margin-top: 0pt; margin-bottom: 0pt;"><span style="font-size: 19px; color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">In order to gather research momentum from the UbiComp community, we call for papers discussing how to design or incorporate different input modalities or sensations for enabling a more immersive VR/AR experience. To reduce long-haul travel following the pandemic, MIMSVAI 2022 will be a virtual workshop held on September 11th for on-site as well as online attendees, following a half-day format. Those expecting to attend the UbiComp/ISWC 2022 conference may also register during this workshop. We arrange sessions for each accepted paper to be presented in the workshop. At the end of each session, we will assign session chairs to mediate the discussion to distill motivated ideas and findings to the participants. &nbsp;</span></p><br><p dir="ltr" style="line-height: 1.7; margin-top: 0pt; margin-bottom: 0pt;"><span style="font-size: 19px; color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Besides the regular sessions, we will invite one expert for VR/AR interactions to give talks in the morning. We hope the keynote presentation can bring in more interesting perspectives regarding VR/AR-related researches. Finally, we will deliver the closing remarks and give the best paper award to the best paper presented at the MIMSVAI 2022 workshop to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</span></p>',
                2021: '<p dir="ltr" style="line-height: 1.7; margin-top: 0pt; margin-bottom: 0pt;"><span style="font-size: 19px; color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">In order to gather research momentum from the UbiComp community, we call for papers discussing how to design or incorporate different input modalities or sensations for enabling a more immersive VR/AR experience. We arrange sessions for each accepted paper to be presented in the workshop. At the end of each session, we will assign session chairs to mediate the discussion to distill motivated ideas and findings to the participants.&nbsp;</span></p><br><p dir="ltr" style="line-height: 1.7; margin-top: 0pt; margin-bottom: 0pt;"><span style="font-size: 19px; color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Besides the regular sessions, we will invite two experts for VR/AR interactions to give talks in the morning and the afternoon, respectively. We hope these two keynote presentations can bring in more interesting perspectives regarding VR/AR-related researches. Finally, a panel discussion will be held in the late afternoon. We will invite 3-5 panelists from both academia and industrial sides to share their valuable experiences related to current research or development trends of VR/AR applications. </span></p>'
            }
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import "@/style/value.sass"

</style>



