>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Multimodal capturing and reconstruction</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Scene description and management issues</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Storytelling</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Tracking and sensing</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Audio interfaces, sound rendering, spatialized audio, auditory perception and psychoacoustics</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Embodied agents, virtual humans and (self-)avatars</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-