 text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Teleoperation and telepresence</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">3D user interaction</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">3D UI metaphors</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Collaborative interactions</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Ethical issues</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Human factors and ergonomics</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Input devices</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Locomotion and navigation</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Multimodal/cross-modal interaction and perception</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Non-fatiguing 3D UIs</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Non-visual interfaces (such as olfactory)</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Touch, tangible and gesture interfaces</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Usage research, evaluation methods and empirical studies</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Domain-specific VR/AR applications.</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Design process, platform/tools, or libraries used to incorporate input modalities or sensations for VR/AR interactions or studying their use</span></span></li></ul><h1 dir="ltr" style="margin-top: 18pt; margin-bottom: 6pt; line-height: 1.7;"><span style="font-size: 30px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Important Dates</span></span></h1><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Paper submission: 23:59 (AoE), </span><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: line-through; vertical-align: baseline; white-space: pre-wrap;">June 15, 2021</span>&nbsp<span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: line-through; vertical-align: baseline; white-space: pre-wrap;">June 22, 2021 </span><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;"> June 30, 2021 </span><span style="font-weight: 400; color: #ff0000;">(extended and final)</span></span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Paper notification: 23:59 (AoE), July 15, 2021</span></span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Camera-ready: 23:59 (AoE), <span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: line-through; vertical-align: baseline; white-space: pre-wrap;">July 31, 2021  Aug. 6, 2021 </span><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Aug. 10, 2021 </span><span style="font-weight: 400; color: #ff0000;">(extended)</span></span></span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Workshop: September 26, 2021</span></span></p><h1 dir="ltr" style="margin-top: 18pt; margin-bottom: 6pt; line-height: 1.7;"><span style="font-size: 30px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Best Paper Award</span></span></h1><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">The Best Paper Award is given to the best paper presented at the MIMSVAI 2021 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</span></span></p><h2 dir="ltr" style="margin-top: 18pt; margin-bottom: 6pt; line-height: 1.7;"><span style="font-size: 30px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Submission Guidelines</span></span></h2><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Please submit your paper via this EasyChair link: </span><br><a href="https://easychair.org/conferences/?conf=mimsvai2021" style="text-decoration:none;"><span style=" color: rgb(17, 85, 204); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: underline; text-decoration-skip-ink: none; vertical-align: baseline; white-space: pre-wrap;">https://easychair.org/conferences/?conf=mimsvai2021</span></a></span></p><br><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;"><strong  style="color: red;">All papers need to be anonymized.</strong> Please submit papers with a maximum length of 5 pages (4-page + 1 references) in </spsn><a href="https://ubicomp.org/ubicomp2021/cfp/template-information/"><span style=" color: rgb(17, 85, 204); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: underline; text-decoration-skip-ink: none; vertical-align: baseline; white-space: pre-wrap;">ACM SIGCHI Master Article template with 2 columns</span></a><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">. Please contact us (<a href="mailto:ubicomp.mimsvai@gmail.com"><span style=" color: rgb(17, 85, 204); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: underline; text-decoration-skip-ink: none; vertical-align: baseline; white-space: pre-wrap;">ubicomp.mimsvai@gmail.com</span></a>) if you have any problems when preparing your submissions.</span></span></p><br><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;  color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">At least one author of each accepted paper needs to register for the conference and the workshop itself. During the workshop, each paper will be presented briefly by one of the authors. And, the Best Paper Award will be given to the best paper presented at the MIMSVAI 2021 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</span></p><br><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;  color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">The accepted papers will be published in the UbiComp/ISWC Adjunct Proceedings, which will be included in the ACM Digital Library as part of the UbiComp conference supplemental proceedings.</span></p>'
            }
        }
    },
    methods: {

    }
}
</script>

